// src/components/Photo.js
import React from "react";
import styled from "styled-components";

const PhotoContainer = styled.div`
  width: 400px; //    400px 
  height: 400px; //    400px 
  margin: 0 auto;
  overflow: hidden; //    
  img {
    width: 100%; //      
    height: 100%; //      
    object-fit: cover; //       
  }
`;

const Photo = ({ imageUrl }) => {
  return (
    <PhotoContainer>
      <img src={imageUrl} alt="Instagram Post" />
    </PhotoContainer>
  );
};

export default Photo;
