import React, { useState } from "react";
import Feed from "./Feed";
import PostCreate from "./PostCreate";
import moment from "moment";
import styled from "styled-components";

const WriteButton = styled.button`
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #5f89f5;
  color: white;
  font-size: 24px;
  border: none;
  position: fixed;
  bottom: 80px;
  right: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  z-index: 10;
`;

const CommunityContainer = () => {
  //   
  const initialPosts = [
    {
      username: "",
      caption: "      !",
      imageUrl: "/foodiimage.jpg",
      userImageUrl: "/pro.jpg",
      comments: [
        {
          commentUsername: "",
          commentUserImageUrl: "/pro.jpg",
          commentText: " !  ~~~",
        },
      ],
      postDate: moment().format("LL"),
      onEdit: () => {
        //       
        //      .
        console.log("  .");
      },
    },
    {
      username: "",
      caption: "  .",
      imageUrl: "/foodddd.jpg",
      userImageUrl: "/prett.jpg",
      comments: [
        {
          commentUsername: "",
          commentUserImageUrl: "",
          commentText: "",
        },
      ],
      postDate: moment().format("LL"),
      onEdit: () => {
        //       
        //      .
        console.log("  .");
      },
    },
  ];

  // posts useState .
  const [posts, setPosts] = useState(initialPosts);
  const [isCreatingPost, setIsCreatingPost] = useState(false);

  const handleWriteButtonClick = () => {
    setIsCreatingPost(true);
  };

  const handlePostSubmission = (newPost) => {
    setPosts([newPost, ...posts]);
    setIsCreatingPost(false);
  };

  const handleDeletePost = (indexToDelete) => {
    setPosts((prevPosts) =>
      prevPosts.filter((_, index) => index !== indexToDelete)
    );
  };

  if (isCreatingPost) {
    return <PostCreate onSubmit={handlePostSubmission} />;
  }

  return (
    <div>
      {posts.map((post, index) => (
        <Feed
          key={index}
          username={post.username}
          caption={post.caption}
          imageUrl={post.imageUrl}
          comments={post.comments}
          userImage={post.userImageUrl}
          postDate={post.postDate}
          onDelete={() => handleDeletePost(index)}
        />
      ))}
      <WriteButton onClick={handleWriteButtonClick}>+</WriteButton>
    </div>
  );
};

export default CommunityContainer;
