import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { Add } from "iconic-react";

const HeaderWrapper = styled.header`
  background-color: #fff;
  color: #000;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  justify-content: center; //     .
  align-items: center;
  text-align: center;
  border-bottom: 1px solid #f2f2f2;
`;

const BackButton = styled.div`
  //  Link div 
  color: #000;
  text-decoration: none;
  font-size: 16px;
  position: absolute;
  left: 20px;
  cursor: pointer;
`;

const DietAddButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px; //   
  height: 40px; //   
  border-radius: 50%; //    
  background-color: #5f89f5; //   
  position: absolute;
  right: 20px;
`;

const DietAddbutton = styled(Add)`
  color: #fff;
  transform: scale(1.4);
`;

const Title = styled.h1`
  font-size: 24px;
  margin: 0;
`;

const AppHeader = ({ backButton, title, type, addEvent }) => {
  const navigate = useNavigate(); //    navigate .

  return (
    <HeaderWrapper>
      {backButton && (
        <BackButton onClick={() => navigate(-1)}></BackButton> // navigate(-1)    .
      )}
      <Title>{title}</Title>
      {type === "diet" && (
        <DietAddButtonContainer onClick={addEvent}>
          <DietAddbutton size={20} />
        </DietAddButtonContainer>
      )}
    </HeaderWrapper>
  );
};

export default AppHeader;
