import React, { useState } from "react";
import SignupTitle1 from "../components/signup/SignupTitle1";
import styled from "styled-components";
import IconInput from "../components/signup/IconInput";
import ButtonComponent from "../components/signup/ButtonComponent";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { observer } from "mobx-react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const PageBlock = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 46px;
`;

const InputForm = styled.div`
  display: flex;
  width: 340px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Text = styled.div`
  font-size: 14px;
`;

const LoginPage = observer(() => {
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();

  const navigate = useNavigate();

  const handleLoginButtonClick = () => {
    console.log(email, password);
    //     
    let requestOption = {
      url: "api/v1/user/signin",
      method: "POST",
      headers: {},
      data: {
        mail: email,
        password: password,
      },
    };
    axios(requestOption).then((response) => {
      console.log(response.data);
      if (response.status === 200) {
        alert(" ");
        localStorage.setItem("userId", response.data.result);

        //  
        navigate("/main");
      } else {
        alert(response.data.errorMsg);
      }
    });
  };
  const handleEmailChange = (e) => {
    const value = e.target.value;
    // input    useState   
    setEmail(value);
  };
  const handlePasswordChange = (e) => {
    const value = e.target.value;
    // input    useState   
    setPassword(value);
  };

  return (
    <PageBlock>
      <SignupTitle1 title={"LOGIN"} logo={"PROTEIN BODY"}></SignupTitle1>
      <InputForm>
        <IconInput
          icontype={"sms"}
          placeholder={""}
          //handleChanges   
          handleChange={handleEmailChange}
        ></IconInput>
        <IconInput
          icontype={"lock"}
          placeholder={""}
          handleChange={handlePasswordChange}
        ></IconInput>
      </InputForm>
      <br></br>
      <Text>
        <Link> ?</Link>
      </Text>
      <ButtonContainer>
        {/* handleLoginButtonClick   */}
        <ButtonComponent
          text=""
          handleClick={handleLoginButtonClick}
        ></ButtonComponent>
      </ButtonContainer>
      <Text>
          ? <Link  to="/signup"></Link>
      </Text>
    </PageBlock>
  );
});

export default LoginPage;
